import * as React from 'react';
import * as serviceWorkerRegistration from "./src/serviceWorkerRegistration";

import {
  Avatar,
  DefaultTheme,
  Provider as PaperProvider,
} from 'react-native-paper';

import AsyncStorage from '@react-native-async-storage/async-storage';
import AuthContext from './AppContext';
import HomeScreen from './src/screens/Home/HomeScreen';
import { Image } from 'react-native';
import LoginScreen from './src/screens/Login/LoginScreen';
import { NavigationContainer } from '@react-navigation/native';
import SplashScreen from './src/screens/SplashScreen';
import { TouchableOpacity } from 'react-native-gesture-handler';
import alert from './src/components/Alert';
import { colors } from './src/constants/colors';
import { createStackNavigator } from '@react-navigation/stack';

const Stack = createStackNavigator();

const theme = {
  ...DefaultTheme,
  roundness: 2,
  colors: {
    ...DefaultTheme.colors,
    primary: colors.primary,
    accent: colors.secondary,
    background: colors.white,
  },
};

interface IActionA {
  type: string;
  token?: string | null;
}
export default function App() {
  const [state, dispatch] = React.useReducer(
    (prevState: any, action: IActionA) => {
      switch (action.type) {
        case 'RESTORE_TOKEN':
          return {
            ...prevState,
            userToken: action.token,
            isLoading: false,
          };
        case 'SIGN_IN':
          return {
            ...prevState,
            isSignout: false,
            userToken: action.token,
          };
        case 'SIGN_OUT':
          return {
            ...prevState,
            isSignout: true,
            userToken: null,
          };
      }
    },
    {
      isLoading: true,
      isSignout: false,
      userToken: null,
    },
  );

  React.useEffect(() => {
    // Fetch the token from storage then navigate to our appropriate place
    const bootstrapAsync = async () => {
      let userToken;
      try {
        // Restore token stored in `SecureStore` or any other encrypted storage
        userToken = await AsyncStorage.getItem('userToken');
      } catch (e) {
        // Restoring token failed
      }

      // After restoring token, we may need to validate it in production apps

      // This will switch to the App screen or Auth screen and this loading
      // screen will be unmounted and thrown away.
      dispatch({ type: 'RESTORE_TOKEN', token: userToken });
    };

    bootstrapAsync();
  }, []);

  const authContext = React.useMemo(
    () => ({
      signIn: async (data: any) => {
        // In a production app, we need to send some data (usually username, password) to server and get a token
        // We will also need to handle errors if sign in failed
        // After getting token, we need to persist the token using `SecureStore` or any other encrypted storage
        // In the example, we'll use a dummy token
        await AsyncStorage.setItem('userToken', JSON.stringify(data));
        dispatch({ type: 'SIGN_IN', token: JSON.stringify(data) });
      },
      signOut: async () => {
        console.log('testsss')
        await AsyncStorage.removeItem('userToken');
        dispatch({ type: 'SIGN_OUT' });
      },
      userToken: state.userToken,
    }),
    [state],
  );

  return (
    <AuthContext.Provider value={authContext}>
      <PaperProvider theme={theme}>
        <NavigationContainer documentTitle={{
          formatter: (options, route) =>
            `${options?.title ?? route?.name} - LSA`,
        }}>
          <Stack.Navigator>
            {state.isLoading ? (
              // We haven't finished checking for the token yet
              <Stack.Screen
                name="Splash"
                component={SplashScreen}
                options={{
                  header: () => null,
                }}
              />
            ) : state.userToken == null ? (
              // No token found, user isn't signed in
              <Stack.Screen
                name="SignIn"
                component={LoginScreen}
                options={{
                  header: () => null,
                  // title: 'Sign in',
                  // When logging out, a pop animation feels intuitive
                  animationTypeForReplace: state.isSignout ? 'pop' : 'push',
                }}
              />
            ) : (
              // User is signed in
              <Stack.Screen
                name="Home"
                component={HomeScreen}
                options={{
                  // headerTitle: () => (
                  //   <Image
                  //     source={require('./src/assets/logo-transparent.png')}
                  //     style={{width: 30, height: 30}}
                  //   />
                  // ),
                  headerTintColor: colors.secondary,
                  headerLeft: () => (
                    // <Avatar.Icon
                    //   size={30}
                    //   icon="account-circle"
                    //   color={colors.secondary}
                    //   style={{marginLeft: 10}}
                    // />
                    <Image
                      source={require('./src/assets/logo-transparent.png')}
                      style={{ width: 40, height: 40, margin: 5 }}
                    />
                  ),
                  headerRight: () => (
                    <TouchableOpacity
                      onPress={() => {
                        alert(
                          'Logout',
                          'Are you sure you want to logout?',
                          [
                            {
                              text: 'Cancel',
                              onPress: () => console.log('Cancel Pressed'),
                              style: 'cancel',
                            },
                            {
                              text: 'OK',
                              onPress: () => { console.log('first'); authContext.signOut() },
                            },
                          ],
                        );
                      }}>
                      <Avatar.Icon
                        size={30}
                        icon="logout"
                        style={{ marginLeft: 10, margin: 10 }}
                      />
                    </TouchableOpacity>
                  ),
                }}
              />
            )}
          </Stack.Navigator>
        </NavigationContainer>
      </PaperProvider>
    </AuthContext.Provider>
  );
}


serviceWorkerRegistration.register();