import "react-datepicker/dist/react-datepicker.css";

import {
  ActivityIndicator,
  Card,
  Paragraph,
  TextInput,
} from 'react-native-paper';
import React, { useEffect, useState } from 'react';
import { StyleSheet, Text, TouchableOpacity, View } from 'react-native';

// import DateTimePickerModal from "react-native-modal-datetime-picker";
import DatePicker from "react-datepicker";
import DropDownPicker from 'react-native-dropdown-picker';
import { Helmet } from "react-helmet";
import MaterialIcon from 'react-native-vector-icons/MaterialIcons';
import apiHandler from '../../helpers/apiHandler';
import { colors } from '../../constants/colors';
import { formatDate } from '../../constants/utils';
import { COMPANIES } from "../../constants/urls";

interface ICompany {
  Id: string;
  Name: string;
  Display_Name: string;
  Business_Profile_Id: string;
}

// const today = formatDate(new Date());
// const yesterday = formatDate(new Date(Date.now() - 86400000));
// const todayBeforeYesterday = formatDate(new Date(Date.now() - 2 * 86400000));
const HomeScreen = () => {
  const [companiesList, setCompaniesList] = useState<any>([]);
  const [showCompanyDropDown, setCompanyDropDown] = useState(false);
  const [selectedCompany, setSelectedCompany] = useState<string | null>(null);


  const [locationList, setLocationList] = useState([]);
  const [showLocationsDropDown, setLocationsDropDown] = useState(false);
  const [selectedLocation, setSelectedLocation] = useState<string>('');

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [valueEntries, seValueEntries] = useState([]);
  const [totalTransaction, setTotalTransaction] = useState<number | null>(null);
  const [totalSalesQty, setTotalSalesQty] = useState<number | null>(null);
  const [totalSalesPrice, setTotalSalesPrice] = useState<number | null>(null);

  const [date, setDate] = useState<Date>(new Date());
  const [open, setOpen] = useState(false);

  // const [periodList] = useState([
  //   {label: 'Today', value: today},
  //   {label: 'Yesterday', value: yesterday},
  //   {label: 'Day before yesterday', value: todayBeforeYesterday},
  // ]);
  // const [showPeriodsDropDown, setPeriodsDropDown] = useState(false);
  const [selectedPeriod, setSelectedPeriod] = useState<string>('');

  const [loading, setLoading] = useState(false);

  useEffect(() => {
    const getValueEntries = async () => {
      setLoading(true);
      const findCompany = companiesList.find((cp: any) => cp.id === selectedCompany)
      let { baseUrl, instance, url2 } = findCompany;
      apiHandler(
        'get',
        `${baseUrl}${instance}${url2}`,
        `/companies(${selectedCompany})/ValueEntries?$filter=locationCode eq '${selectedLocation}' and postingDate eq ${selectedPeriod}`,
        null,
      )
        .then(response => {
          setLoading(false);

          console.log(response);
          if (response.data.value) {
            seValueEntries(response.data.value);
            const allTransactions = response.data.value;
            const allTransactionsWithoutSalesCreditMemo =
              allTransactions.filter(
                (x: any) => x.documentType === 'Sales Invoice',
              );
            setTotalTransaction(
              new Set(
                allTransactionsWithoutSalesCreditMemo.map(
                  (entry: any) => entry.documentNo,
                ),
              ).size,
            );
            setTotalSalesQty(
              response.data.value.reduce((p: any, v: any) => {
                return p + v.invoicedQty * -1;
              }, 0),
            );
            setTotalSalesPrice(
              response.data.value
                .reduce((p: any, v: any) => {
                  return p + v.salesAmountActual;
                }, 0)
                .toFixed(2),
            );
          } else {
            seValueEntries([]);
            setTotalTransaction(0);
            setTotalSalesQty(0);
            setTotalSalesPrice(0);
          }
        })
        .catch(err => {
          console.log(err);
          setLoading(false);
        });
    };
    if (selectedCompany && selectedLocation && selectedPeriod) {
      getValueEntries();
    }
    console.log('object', selectedCompany, selectedLocation, selectedPeriod);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedCompany, selectedLocation, selectedPeriod]);

  useEffect(() => {
    const getLocations = async () => {
      setLoading(true);
      const findCompany = companiesList.find((cp: any) => cp.id === selectedCompany)
      console.log('first', findCompany, selectedCompany, selectedCompany);
      let { baseUrl, instance, url1 } = findCompany;
      apiHandler(
        'get',
        `${baseUrl}${instance}${url1}`,
        `/Company('${selectedCompany}')/locationList?$filter=Use_As_In_Transit eq false`,
        null,
      )
        .then(response => {
          setLoading(false);

          console.log(response);
          setLocationList(
            response.data.value.map(
              ({ Name, Code }: { Name: string; Code: string }) => {
                return { label: Name ? Name : Code, value: Code };
              },
            ),
          );
        })
        .catch(err => {
          console.log(err);
          setLoading(false);
        });
    };
    if (selectedCompany) {
      getLocations();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedCompany]);

  useEffect(() => {
    if (selectedCompany && selectedLocation) {
      if (!selectedPeriod) {
        setSelectedPeriod(formatDate(new Date()));
      }
    }
    console.log(selectedCompany, selectedLocation);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedCompany, selectedLocation]);

  useEffect(() => {
    const getCompanies = async () => {
      setLoading(true);
      COMPANIES.forEach(company => {
        let { baseUrl, instance, url1 } = company;
        apiHandler('get', `${baseUrl}${instance}${url1}`, '/Company', null)

          .then(response => {
            setLoading(false);
            setCompaniesList((prev: any) => {
              const allCompanies = response.data.value.map(
                ({
                  Id,
                  Name,
                  Display_Name,
                  Business_Profile_Id,
                }: ICompany) => {
                  return { id: Id, label: Display_Name, value: Id, ...company };
                },
              );
              return [...prev, ...allCompanies]
            }
            );
          })
          .catch(err => {
            console.log(err);
            setLoading(false);
          });
      });

    };
    getCompanies();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    const comp = companiesList.find(
      (c: any) => c.value === selectedCompany,
    ) || {
      id: '',
    };
    console.log(comp, 'company', companiesList, selectedCompany);
    setSelectedCompany(comp?.id);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedCompany]);

  useEffect(() => {
    setSelectedPeriod(formatDate(date));
  }, [date]);

  return (
    <View>
      <Helmet>
        <meta charSet="utf-8" name="title" content={'LSA'} />
        <title>LSA app</title>
      </Helmet>
      <View style={styles.filtersContainer}>
        <DropDownPicker
          zIndex={3000}
          zIndexInverse={1000}
          style={styles.dropDown}
          placeholder="Select Company"
          placeholderStyle={{ color: colors.gray }}
          open={showCompanyDropDown}
          onOpen={() => setLocationsDropDown(false)}
          value={selectedCompany}
          items={companiesList}
          setOpen={setCompanyDropDown}
          setValue={setSelectedCompany}
        />

        <DropDownPicker
          zIndex={2000}
          zIndexInverse={2000}
          style={styles.dropDown}
          placeholder="Select Location"
          placeholderStyle={{ color: colors.gray }}
          onOpen={() => setCompanyDropDown(false)}
          open={showLocationsDropDown}
          value={selectedLocation}
          items={locationList}
          setOpen={setLocationsDropDown}
          setValue={setSelectedLocation}
        />
        { }
        <TouchableOpacity
          // onPress={() => setOpen(true)}
          style={{ flexDirection: 'row', justifyContent: 'space-between', padding: 5 }}>
          <View style={{ marginLeft: 10 }}>
            <Text style={{ fontSize: 12, marginBottom: 5, color: colors.gray }}>
              Select Date
            </Text>
            {/* <Text style={{ fontSize: 16 }}>{selectedPeriod}</Text> */}
          </View>
          <View style={{}}>
            <DatePicker selected={date} onChange={(dt: Date) => {
              console.log(dt);
              // setOpen(false);
              setDate(dt);
            }} />
          </View>
        </TouchableOpacity>



        {/* <DateTimePickerModal
          isVisible={open}
          mode="date"
          date={date}
          onConfirm={dt => {
            setOpen(false);
            setDate(dt);
          }}
          onCancel={() => {
            setOpen(false);
          }}
        /> */}
        {/* <DatePicker
          modal
          maximumDate={new Date()}
          mode="date"
          open={open}
          date={date}
          onConfirm={dt => {
            setOpen(false);
            setDate(dt);
          }}
          onCancel={() => {
            setOpen(false);
          }}
        /> */}
      </View>
      {loading ? <ActivityIndicator color={colors.primary} /> : null}
      {!(totalSalesQty && totalSalesPrice && totalTransaction) ? (
        <Text style={{ textAlign: 'center', marginTop: 50, color: colors.gray }}>
          No data found
        </Text>
      ) : (
        <View style={styles.contentContainer}>
          {/* {totalSalesQty ? ( */}
          <Card style={{ marginTop: 8 }}>
            <Card.Title title="Sales Qty" />
            <Card.Content>
              <Paragraph>{totalSalesQty}</Paragraph>
            </Card.Content>
          </Card>
          {/* ) : null} */}
          {/* {totalSalesPrice ? ( */}
          <Card style={{ marginTop: 8 }}>
            <Card.Title title="Sales Value" />
            <Card.Content>
              <Paragraph>NPR. {totalSalesPrice}</Paragraph>
            </Card.Content>
          </Card>
          {/* ) : null} */}
          {/* {totalTransaction ? ( */}
          <Card style={{ marginTop: 8 }}>
            <Card.Title title="Total Transactions" />
            <Card.Content>
              <Paragraph> {totalTransaction}</Paragraph>
            </Card.Content>
          </Card>
          {/* ) : null} */}
        </View>
      )}
    </View>
  );
};

export default HomeScreen;

const styles = StyleSheet.create({
  filtersContainer: {
    padding: 15,
    zIndex: 1000,
  },
  filterStyle: {
    flex: 1,
    margin: 5,
  },
  dropDown: {
    marginBottom: 10,
    zIndex: 2,
    borderColor: colors.gray,
  },
  contentContainer: {
    marginTop: 30,
    justifyContent: 'center',
    padding: 10,
    zIndex: 2,
  },
});
