import {
  ActivityIndicator,
  Image,
  ImageBackground,
  StyleSheet,
  Text,
  View,
} from 'react-native';
import React, { useEffect } from 'react';

import AuthContext from '../../../AppContext';
import { Helmet } from 'react-helmet'
import { TextInput } from 'react-native-paper';
import { TouchableOpacity } from 'react-native-gesture-handler';
import apiHandler from '../../helpers/apiHandler';
import { colors } from '../../constants/colors';
import { COMPANIES } from '../../constants/urls';

let timer: ReturnType<typeof setTimeout>;

function LoginScreen() {
  const [username, setUsername] = React.useState(''); //APPUSER
  const [password, setPassword] = React.useState(''); //Lsa@2021
  const [showPassword, setShowPassword] = React.useState(false);
  const [errorMessage, setErrorMessage] = React.useState('');
  const [logginIn, setLoggingIn] = React.useState(false);

  const { signIn } = React.useContext(AuthContext);

  useEffect(() => {
    return () => {
      clearTimeout(timer);
    };
  }, []);
  return (
    <ImageBackground
      source={require('../../assets/logo1.jpg')}
      blurRadius={30}
      style={styles.container}>
      <Helmet>
        <meta charSet="utf-8" name="title" content={'LSA'} />
        <title>LSA app</title>
      </Helmet>
      <View style={styles.center}>
        <Image
          source={require('../../assets/logo-transparent.png')}
          style={{ height: 180, width: 180 }}
        />
      </View>
      <View style={{ marginTop: 20, alignItems: 'center' }}>
        <TextInput
          style={styles.loginInput}
          mode="flat"
          label="Username"
          value={username}
          onChangeText={text => setUsername(text)}
        />
        <TextInput
          style={styles.loginInput}
          mode="flat"
          label="Password"
          value={password}
          onChangeText={text => setPassword(text)}
          secureTextEntry={!showPassword}
          right={
            <TextInput.Icon
              name="eye"
              onPress={() => setShowPassword(!showPassword)}
            />
          }
        />
      </View>
      <View style={styles.center}>
        <Text style={{ marginTop: 10, color: colors.red }}>{errorMessage}</Text>
        <TouchableOpacity
          onPress={() => {
            setErrorMessage('');
            setLoggingIn(true);

            timer = setTimeout(() => {
              let { baseUrl, instance, url1 } = COMPANIES[0];
              apiHandler('get', `${baseUrl}${instance}${url1}`, '/Company', { username, password })
                .then(() => {
                  setLoggingIn(false);
                  signIn({ username, password });
                })
                .catch(() => {
                  setErrorMessage('Invalid username or password');
                  setLoggingIn(false);
                });
            }, 200);
          }}
          style={styles.signInButtonContainer}>
          {logginIn ? (
            <View style={styles.signInText}>
              <ActivityIndicator color={colors.white} />
            </View>
          ) : (
            <Text style={styles.signInText}>Sign In</Text>
          )}
        </TouchableOpacity>
      </View>
    </ImageBackground>
  );
}

export default LoginScreen;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    backgroundColor: colors.white,
    padding: 10,
  },
  center: { justifyContent: 'center', alignItems: 'center' },
  loginInput: {
    marginTop: 5,
    padding: 5,
    width: 300,
    height: 50,
    borderRadius: 10,
    backgroundColor: colors.white,
  },
  signInButtonContainer: {
    backgroundColor: colors.secondary,
    justifyContent: 'center',
    alignItems: 'center',
    marginTop: 20,
    borderRadius: 4,
    width: 300,
  },
  signInText: { color: 'white', padding: 10, fontSize: 18 },
});
