import { API_URL, API_URL_1 } from "../constants/urls";

import AsyncStorage from "@react-native-async-storage/async-storage";
import Base64 from "../helpers/base64";
import axios from "axios";

export const getHeader = async (usr, form) => {
  let header = {};
  try {
    let user;
    if (usr) {
      user = usr;
    } else {
      user = JSON.parse(await AsyncStorage.getItem("userToken"));
    }
    if (user) {
      header = {
        Authorization:
          "Basic " + Base64.btoa(`${user?.username}:${user?.password}`),
      };
    }
  } catch (e) {
    // Restoring token failed
  }

  console.log(header);

  if (!form) {
    header = {
      ...header,
      "Content-Type": "application/json",
    };
  } else {
    header = {
      ...header,
      "Content-Type": "multipart/form-data",
      Accept: "multipart/form-data",
    };
  }

  return header;
};

/**
 * Create Axios Request handler
 * @param requestType
 * @param url
 * @param {object | null} user
 * @param {object} data
 * @param params
 * @param user
 */
const apiHandler = async (
  requestType,
  baseURL,
  url,
  user = null,
  data = undefined,
  params = undefined,
  onUploadProgress = undefined
) => {
  const headers = await getHeader(user);
  let config = {
    baseURL,
    url,
    method: requestType,
    headers,
    data,
    params,
    onUploadProgress,
    transformRequest: [
      function (dt, hd) {
        // Do whatever you want to transform the data

        return data;
      },
    ],
  };
  if (!data) {
    delete config.data;
  }
  if (!params) {
    delete config.params;
  }
  if (!onUploadProgress) {
    delete config.onUploadProgress;
  }
  console.log(config);
  return axios(config);
};

export default apiHandler;
